.todo-list-item {
	font-size: 1.25rem;
	display: flex;
}

.todo-list-item button {
   width: 35px;
	margin: 3px;
}

.todo-list-item button.red {
	border-color: tomato;
	color: tomato;
}
.todo-list-item button.red:active, .todo-list-item button.red:hover {
	color: #ffffff;
	background-color: tomato;
}

.todo-list-item-label {
   margin-left: 1.25rem;
   line-height: 35px;
   cursor: pointer;
	user-select: none;
	margin-right: auto;
	flex-grow: 1;
	justify-content: flex-start;
}
.todo-list-item.done .todo-list-item-label {
   text-decoration: line-through;
}
.todo-list-item.important .todo-list-item-label {
	font-weight: bold;
	color: steelblue;
}
